.form-page-container {
  max-width: 700px;
  margin: 0 auto;
  padding: 2rem;
}

.form-page-container h1 {
  color: #1e90ff;
  text-align: center;
  margin-bottom: 2rem;
}

.form-type-selector {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 2rem;
  justify-content: center;
  flex-wrap: wrap;
}

.type-btn {
  padding: 0.75rem 1.5rem;
  background-color: #f5f5f5;
  border: 2px solid #ddd;
  border-radius: 0.5rem;
  cursor: pointer;
  font-weight: 600;
  color: #666;
  transition: all 0.2s;
}

.type-btn:hover {
  border-color: #1e90ff;
  color: #1e90ff;
}

.type-btn.active {
  background-color: #1e90ff;
  color: white;
  border-color: #1e90ff;
}

.creation-form {
  background: white;
  border: 1px solid #ddd;
  border-radius: 0.75rem;
  padding: 2rem;
  margin-bottom: 2rem;
}

.form-group {
  margin-bottom: 1.5rem;
}

.form-group label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: 600;
  color: #333;
}

.form-group input,
.form-group textarea,
.form-group select {
  width: 100%;
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-family: inherit;
  box-sizing: border-box;
  transition: border-color 0.2s;
}

.form-group input:focus,
.form-group textarea:focus,
.form-group select:focus {
  outline: none;
  border-color: #1e90ff;
  box-shadow: 0 0 0 3px rgba(30, 144, 255, 0.1);
}

.form-help {
  display: block;
  margin-top: 0.5rem;
  color: #999;
  font-size: 0.9rem;
}

.form-error {
  background-color: #fee;
  color: #c33;
  border: 1px solid #fcc;
  padding: 1rem;
  border-radius: 0.25rem;
  margin-bottom: 1rem;
}

.form-success {
  background-color: #efe;
  color: #3c3;
  border: 1px solid #cfc;
  padding: 1rem;
  border-radius: 0.25rem;
  margin-bottom: 1rem;
}

.submit-btn {
  width: 100%;
  padding: 0.75rem;
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 0.25rem;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: background-color 0.2s;
}

.submit-btn:hover:not(:disabled) {
  background-color: #0d65d6;
}

.submit-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.form-info {
  background-color: #f9f9f9;
  border-left: 4px solid #1e90ff;
  padding: 1.5rem;
  border-radius: 0.25rem;
}

.form-info h3 {
  margin-top: 0;
  color: #333;
}

.form-info p {
  color: #666;
  margin: 0;
  line-height: 1.6;
}
