.data-display-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.data-display-container h1 {
  color: #1e90ff;
  margin-bottom: 2rem;
}

.tabs {
  display: flex;
  gap: 0.5rem;
  margin-bottom: 2rem;
  border-bottom: 2px solid #ddd;
}

.tab {
  background: none;
  border: none;
  padding: 0.75rem 1.5rem;
  font-size: 1rem;
  cursor: pointer;
  color: #666;
  border-bottom: 3px solid transparent;
  transition: all 0.2s;
}

.tab:hover {
  color: #1e90ff;
}

.tab.active {
  color: #1e90ff;
  border-bottom-color: #1e90ff;
}

.data-section {
  background: white;
  border: 1px solid #ddd;
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin-bottom: 2rem;
  overflow-x: auto;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
}

.data-table thead {
  background-color: #f5f5f5;
  border-bottom: 2px solid #ddd;
}

.data-table th {
  padding: 1rem;
  text-align: left;
  font-weight: 600;
  color: #333;
}

.data-table td {
  padding: 0.75rem 1rem;
  border-bottom: 1px solid #eee;
}

.data-table tbody tr:hover {
  background-color: #f9f9f9;
}

.actions {
  display: flex;
  gap: 0.5rem;
}

.btn-edit,
.btn-delete {
  background: none;
  border: 1px solid #ddd;
  padding: 0.5rem;
  cursor: pointer;
  border-radius: 0.25rem;
  transition: all 0.2s;
  display: flex;
  align-items: center;
  justify-content: center;
}

.btn-edit {
  color: #1e90ff;
}

.btn-edit:hover {
  background-color: #e6f0ff;
}

.btn-delete {
  color: #ff6347;
}

.btn-delete:hover {
  background-color: #ffe6e6;
}

.loading,
.empty,
.error-message {
  text-align: center;
  padding: 2rem;
  color: #666;
}

.error-message {
  color: #c33;
  background-color: #fee;
  border: 1px solid #fcc;
  border-radius: 0.25rem;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-top: 1.5rem;
}

.pagination-btn {
  padding: 0.5rem 1rem;
  background-color: #1e90ff;
  color: white;
  border: none;
  border-radius: 0.25rem;
  cursor: pointer;
  transition: background-color 0.2s;
}

.pagination-btn:hover:not(:disabled) {
  background-color: #0d65d6;
}

.pagination-btn:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

.page-info {
  color: #666;
  font-weight: 600;
}
